import { API_URL } from '../config/Config'

const getTypes = async (controlTypes: { text: string, label: string, value: any }[]): Promise<void> => {
  //send api request here
  const fetchURL = new URL(
    '/api/type/all',
    API_URL,
  );
  const response = await fetch(fetchURL.href);
  const json = await response.json();
  json.forEach((e: { name: any; description: any; }) => {
    controlTypes.push({ text: e.name, label: e.description, value: e.name })
  });
};
const getEvents = async (events: { text: string, label: string, value: any }[]): Promise<void> => {
  //send api request here
  const fetchURL = new URL(
    '/api/events',
    API_URL,
  );
  const response = await fetch(fetchURL.href);
  const json = await response.json();
  json.forEach((e: { EVENT_ID: any; }) => {
    events.push({ text: e.EVENT_ID, label: e.EVENT_ID, value: e.EVENT_ID })
  });
};
const getDevices = async (devices: { text: string, label: string, value: any, dpins: [number], apins: [string], mask: [object] }[]): Promise<void> => {
  //send api request here
  const fetchURL = new URL(
    '/api/device/all',
    API_URL,
  );
  const response = await fetch(fetchURL.href);
  const json = await response.json();
  json.forEach((e: {
    gpio_analog: [string]; i2c_id: number; name: string; gpio_digital: [number]; gpio_mask : [object] 
}) => {
    devices.push({ text: e.name, label: e.name, value: e.i2c_id, dpins: e.gpio_digital, apins : e.gpio_analog, mask: e.gpio_mask  })
  });
};
export { getTypes, getEvents, getDevices};