import { type MRT_Row } from "material-react-table";

import { InputLabel } from "@mui/material";
import * as React from "react";
import Box from "@mui/material/Box";
import Slider from "@mui/material/Slider";
import { type Control } from "../types/Control";

function valuetext(value: number) {
  return `${value}`;
}

export default function RangeSlider(props: {
  children: never[];
  row: MRT_Row<Control>;
}) {
  const [value, setValue] = React.useState<number[]>([0, 1024]);
  let dValue = [
    props.row.index >= 0 ? props.row._valuesCache["range1"] : 0,
    props.row.index >= 0 ? props.row._valuesCache["range2"] : 1024,
  ];
  const handleChange = (event: Event, newValue: number | number[]) => {
    const row = props.row;
    row._valuesCache["range1"] = value[0];
    row._valuesCache["range2"] = value[1];
    setValue(newValue as number[]);
  };

  return (
    <Box sx={{ width: 380, marginTop: "20px" }}>
      <Slider
        valueLabelDisplay="on"
        min={0}
        max={1024}
        getAriaLabel={() => "Voltage range"}
        value={dValue}
        onChange={handleChange}
        getAriaValueText={valuetext}
      />
      <InputLabel sx={{ textAlign: "center" }}>Voltage Range</InputLabel>
    </Box>
  );
}
