import Content from "./components/Content";
import { QueryClient, QueryClientProvider } from "@tanstack/react-query";

import Grid from "./components/Grid";

import { PowerOff } from "@mui/icons-material";
import AirplanemodeInactiveIcon from "@mui/icons-material/AirplanemodeInactive";
import WifiOffIcon from "@mui/icons-material/WifiOff";
import LogoutIcon from "@mui/icons-material/Logout";
import CloseIcon from "@mui/icons-material/Close";
import {
  BottomNavigation,
  BottomNavigationAction,
  Box,
  IconButton,
  Slide,
} from "@mui/material";

import { ThemeProvider, createTheme } from "@mui/material/styles";
import React from "react";

const darkTheme = createTheme({
  palette: {
    mode: "dark",
  },
});

const queryClient = new QueryClient();
//const wsHome = new WebSocket("wss://avionics.javajet.com/ws");
export default function JJCCDashboard() {
  const [checked, setChecked] = React.useState(false);
  const containerRef = React.useRef<HTMLElement>(null);
  const handleChange = () => {
    setChecked((prev) => !prev);
  };
  //Put this with your other react-query providers near root of your app
  return (
    <Box ref={containerRef}>
      <QueryClientProvider client={queryClient}>
        <ThemeProvider theme={darkTheme}>
          <QueryClientProvider client={queryClient}>
            <Grid checked={checked} />
          </QueryClientProvider>{" "}
          <BottomNavigation id="electron-navigation" showLabels>
            <BottomNavigationAction
              sx={{visibility: 'hidden'}}
              id="simulator-button"
              onClick={handleChange}
              label="Simulator"
              icon={<AirplanemodeInactiveIcon />}
            />
            <BottomNavigationAction
              sx={{visibility: 'hidden'}}
              id="devices-button"
              onClick={handleChange}
              label="Devices"
              icon={<PowerOff />}
            />
            <BottomNavigationAction
              sx={{visibility: 'hidden'}}
              id="services-button"
              onClick={handleChange}
              label="Services"
              icon={<WifiOffIcon />}
            />
            <BottomNavigationAction
              href="/out"
              label="Logout"
              icon={<LogoutIcon />}
            />
          </BottomNavigation>
          <Slide direction="down" in={checked} container={containerRef.current}>
            <Box
              sx={{
                top: "1%",
                left: "20%",
                padding: "20px",
                position: "absolute",
                zIndex: 100,
              }}
            >
              <IconButton
                onClick={() => {
                  setChecked(false);
                }}
                sx={{
                  marginTop: "20px",
                  marginRight: "30px",
                  position: "relative",
                  float: "right",
                }}
              >
                <CloseIcon />
              </IconButton>
              <Content></Content>
            </Box>
          </Slide>
        </ThemeProvider>
      </QueryClientProvider>
    </Box>
  );
}
