//CREATE hook (post new user to api)
function useCreateControl() {
  const queryClient = useQueryClient();
  return useMutation({
    mutationFn: async (values: Control) => {
      console.log("send api create request here");
      let control = {
        _id: "657dc123b4a1c631263d8667", //mongo requires this "fake" object _id
        events: [values.event1, values.event2 ? values.event2 : ""],
        pins: [values.pin1, values.pin2 ? values.pin2 : 0],
        range: [values.range1, values.range2 ? values.range2 : 0],
        type: values.type,
        slave_id: values.slave_id,
      };
      await new Promise((resolve, reject) => {
        fetch("/api/control/save/" + JSON.stringify(control), {
          method: "GET",
          headers: {
            "Content-type": "application/json; charset=UTF-8",
          },
        })
          .then((response) => response.json())
          .then((json) => resolve(json))
          .catch((error) => {
            reject(error);
          });
      });
    },
    //client side optimistic update
    onMutate: (newCInfo: Control) => {
      queryClient.setQueryData(
        ["controls"],
        (prevControls: any) =>
          [
            ...prevControls,
            {
              ...newCInfo,
              id: (Math.random() + 1).toString(36).substring(7),
            },
          ] as Control[]
      );
    },
    // onSettled: () => queryClient.invalidateQueries({ queryKey: ['controls'] }), //refetch controls after mutation, disabled for demo
  });
}

//READ hook (get events from api)
function useGetControls() {
  return useQuery<Control[]>({
    queryKey: ["controls"],
    queryFn: async () => {
      //send api request here
      const fetchURL = new URL("/api/control/all", API_URL);
      const response = await fetch(fetchURL.href);
      const json = await response.json();
      console.log("wtf");
      /*Map array values to distinct control properties*/
      return json.map(
        (e: { [x: string]: any; events: any[]; pins: any[]; range: any[] }) => {
          e["event1"] = e.events[0];
          e["event2"] = e.events[1];
          e["pin1"] = e.pins[0];
          e["pin2"] = e.pins[1];
          e["range1"] = e.range[0];
          e["range2"] = e.range[1];
          return e;
        }
      );
    },
    refetchOnWindowFocus: false,
  });
}

//UPDATE hook (put control in api)
function useUpdateControl() {
  const queryClient = useQueryClient();
  return useMutation({
    mutationFn: async (control: Control) => {
      console.log("send api update request here");
      await new Promise((resolve) => setTimeout(resolve, 1000)); //fake api call
      return Promise.resolve();
    },
    //client side optimistic update
    onMutate: (newControlInfo: Control) => {
      queryClient.setQueryData(["controls"], (prevControls: any) =>
        prevControls?.map((prevControl: Control) =>
          prevControl.id === newControlInfo.id ? newControlInfo : prevControl
        )
      );
    },
    //onSettled: () => queryClient.invalidateQueries({}), //refetch controls after mutation, disabled for demo
  });
}

//DELETE hook (delete user in api)
function useDeleteControl() {
  const queryClient = useQueryClient();
  return useMutation({
    mutationFn: async (userId: string) => {
      console.log("send api delete request here");
      await new Promise((resolve) => setTimeout(resolve, 1000)); //fake api call
      return Promise.resolve();
    },
    //client side optimistic update
    onMutate: (controlId: string) => {
      queryClient.setQueryData(["controls"], (prevControls: any) =>
        prevControls?.filter((c: Control) => c.id !== controlId)
      );
    },
    // onSettled: () => queryClient.invalidateQueries({ queryKey: ['controls'] }), //refetch controls after mutation, disabled for demo
  });
}

