import { MRT_EditActionButtons, MRT_Row, MRT_TableInstance } from "material-react-table";
import { ReactNode } from "react";
import { DialogActions, DialogContent, DialogTitle } from "@mui/material";
import { type Control } from '../types/Control';
import RangeSlider from '../components/Slider';
import React from "react";

const renderEditModal = (props: {
    internalEditComponents: ReactNode[];
    row: MRT_Row<Control>;
    table: MRT_TableInstance<Control>;
}) => {
    return (<>
        <DialogTitle variant="h5">Edit Control</DialogTitle>
        <DialogContent
            sx={{ display: 'flex', flexDirection: 'column', gap: '1.5rem' }}
        >
            {props.internalEditComponents}
            {
            <RangeSlider row={props.row} children={[]}>
            </RangeSlider>
            }
        </DialogContent>
        <DialogActions>
            <MRT_EditActionButtons variant="text" table={props.table} row={props.row} />
        </DialogActions>
    </>
    );
};

const renderAddModal = (props: {
    internalEditComponents: ReactNode[];
    row: MRT_Row<Control>;
    table: MRT_TableInstance<Control>;
}) => { 
    return (
        <>
        <DialogTitle variant="h5">Create New Control</DialogTitle>
        <DialogContent
          sx={{ display: 'flex', flexDirection: 'column', gap: '1rem' }}
        >
          {props.internalEditComponents}
          {
            <RangeSlider row={props.row} children={[]}>
            </RangeSlider>
          }

        </DialogContent>
        <DialogActions>
          <MRT_EditActionButtons variant="text" table={props.table} row={props.row} />
        </DialogActions>
      </>
    );
};

export { renderEditModal, renderAddModal };